import React from 'react';
import fetch from 'isomorphic-unfetch';
import Attendees from './Attendees';
// import styles from './styles/index.css';

class Index extends React.Component {
  constructor(props) {
      super(props);
  }
  static async getInitialProps(props) {
    let res = await fetch(`http://localhost:9000/api/${props.query.eid}/attendees`);
    res = await res.json();
    const data = [];
    for (let i = 0; i < res.length; i += 1) {
      let { row } = res[i];
      row = row.slice(1, row.length - 1);
      row = row.split(',');
      data.push({
        first: row[0],
        last: row[1],
        photourl: row[2],
      });
    }
    console.log(data);
    return {
      eventUsers: data,
      numberOfAttendees: res.length,
    };
  }
  render() {
    return (
      <div>
        <div> 
          <div> Attendees {'(' + this.props.numberOfAttendees + ')'} </div>
          <div> See All </div>
        </div>
        <Attendees eventUsers={this.props.eventUsers} />
      </div>
    );
  }
}

export default Index;



